import React from "react";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import CardColumns from "react-bootstrap/CardColumns";
import Button from "react-bootstrap/Button";
import ButtonGroup from "react-bootstrap/ButtonGroup";
import Dropdown from "react-bootstrap/Dropdown";
import DropdownButton from "react-bootstrap/DropdownButton";

import CampCard from "./CampCard.js";

import { Link } from "react-router-dom";
import { campIdentifications, testData } from "../definitions.js";

export default class DirectoryBody extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filter: "all",
    };
  }
  render() {
    let filterButtonString = this.state.filter;
    if (this.state.filter === "all") {
      filterButtonString = "Filter";
    }

    return (
      <div>
        <Container>
          <Row>
            <Col style={{ marginTop: "2rem", marginBottom: "1rem" }}>
              <h2>
                Camp Directory 2021
                <Link to="/submit">
                  <Button
                    size="sm"
                    variant="outline-success"
                    style={{ marginLeft: "2rem" }}
                  >
                    Submit your camp!
                  </Button>
                </Link>
                <DropdownButton
                  as={ButtonGroup}
                  id="dropdown-basic-button"
                  title={filterButtonString}
                  size="sm"
                  style={{ marginLeft: "2rem" }}
                >
                  {campIdentifications.map((s) => (
                    <Dropdown.Item
                      key={s}
                      onClick={() => this.setState({ filter: s })}
                    >
                      {s}
                    </Dropdown.Item>
                  ))}
                  <Dropdown.Item
                    onClick={() =>
                      this.setState({ filter: "Seeking new members" })
                    }
                  >
                    Seeking new members
                  </Dropdown.Item>
                </DropdownButton>
                {this.state.filter !== "all" && (
                  <Button
                    size="sm"
                    variant="outline-primary"
                    style={{ marginLeft: ".5rem" }}
                    onClick={() => this.setState({ filter: "all" })}
                  >
                    Clear Filter
                  </Button>
                )}
              </h2>
            </Col>
          </Row>
          <Row>
            <Col>
              <CardColumns>
                {testData
                  .filter(
                    (onecamp) =>
                      this.state.filter === "all" ||
                      (this.state.filter === "Seeking new members" &&
                        onecamp.join &&
                        onecamp.join.open) ||
                      this.state.filter === onecamp.identifies
                  )
                  .map((onecamp) => (
                    <CampCard o={onecamp} key={onecamp.name} />
                  ))}
              </CardColumns>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
